@import "src/variables";

.Packages {
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 80vh;
  background-color: $darkBackground;
  position: relative;

  @media (max-width: 768px) {
    flex-direction: column;
    height: auto;
  }

  &__decorate {
    position: absolute;
    top: 30vh;
    left: 0;
    width: 100%;
    height: 20vh;
    background-color: $lightBackground;
    z-index: 0;
    @media (max-width: 768px) {
      display: none;
    }
  }

  &__package {
    z-index: 1;
    background: $yellowGradient;
    border: 2px dashed black;
    width: 320px;
    height: 440px;

    @media (max-width: 768px) {
      width: 280px;
      height: auto;
      margin-bottom: 48px;
    }

    &--bigger {
      width: 320px;
      height: 520px;
      margin: 0 32px;

      @media (max-width: 768px) {
        width: 300px;
        height: auto;
        margin: 0 0 48px 0;
      }
    }
  }

  &__header {
    font-size: 32px;
    font-family: "Nunito", sans-serif;
    font-weight: 700;
    text-align: center;
    padding: 24px 0 36px;
    border-bottom: 2px solid white;
    margin: 0;

    @media (max-width: 768px) {
      font-size: 24px;
    }
  }

  &__list {
    margin: 0;
    padding: 0 0 16px 0;
    list-style: none;
    max-width: 300px;

    @media (max-width: 768px) {
      max-width: 200px;
    }
  }

  &__item {
    font-size: 16px;
    line-height: 24px;
    padding: 16px;
    border-bottom: 1px solid white;
    position: relative;
    margin-left: 16px;

    @media (max-width: 768px) {
      font-size: 12px;
      line-height: 20px;
    }

    &::before {
      display: inline-block;
      width: 20px;
      height: 20px;
      position: absolute;
      top: 16px;
      left: -8px;
      background: url("./../../../assets/star.svg");
      content: '';

      @media (max-width: 768px) {
        top: 14px;
        left: -12px;
      }
    }

    &--last {
      margin-bottom: 24px;
    }
  }
}